





















import Vue from 'vue';

export default Vue.extend({
  name: 'HomePage',
  data() {
    return {
      images: [] as Array<string>,
      numImages: 1,
    };
  },
  methods: {
    uploadImage(event: string, index: number) {
      this.images[index] = event;
      // this.$refs[`img-${index}`].src = event;
    },
    addImage() {
      this.numImages++;
    },
  },
});
